body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Montserrat", sans-serif;
}

body {
  background: url(/static/media/Background2.a1a08994.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.App {
  background: rgba(0, 0, 0, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
}

.Navbar {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 2em 70px;
  width: calc(100% - 140px);
  height: calc(100vh - 4em);
  display: none;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  z-index: 1;
}

.Navbar img {
  float: right;
  width: 30px;
  margin: 10px 0;
  cursor: pointer;
  opacity: 0;
}

.Navbar ul {
  margin: 20% 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: auto auto auto auto;
  list-style-type: none;
}

.Navbar ul li {
  margin: 0;
  padding: 0;
  display: inline-block;
  opacity: 0;
}

.Navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 45px;
  transition: 0.5s;
}

.Navbar ul li a:hover {
  color: gray;
}

.Navbar ul li span {
  color: gray;
  display: block;
  width: 250px;
  cursor: default;
}

.display {
  display: block;
  -webkit-animation: forNavContainer 0.4s ease-in forwards;
          animation: forNavContainer 0.4s ease-in forwards;
}

.display img {
  -webkit-animation: closeBtn 1s 1s forwards;
          animation: closeBtn 1s 1s forwards;
}

.display ul li:nth-of-type(1) {
  -webkit-animation: navElements 1s 0.7s forwards;
          animation: navElements 1s 0.7s forwards;
}

.display ul li:nth-of-type(2) {
  -webkit-animation: navElements 1s 0.9s forwards;
          animation: navElements 1s 0.9s forwards;
}

.display ul li:nth-of-type(3) {
  -webkit-animation: navElements 1s 1.1s forwards;
          animation: navElements 1s 1.1s forwards;
}

.display ul li:nth-of-type(4) {
  -webkit-animation: navElements 1s 1.3s forwards;
          animation: navElements 1s 1.3s forwards;
}

@-webkit-keyframes forNavContainer {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  to {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@keyframes forNavContainer {
  from {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
  }
  to {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@-webkit-keyframes closeBtn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes closeBtn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes navElements {
  from {
    -webkit-transform: translateY(70%);
            transform: translateY(70%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes navElements {
  from {
    -webkit-transform: translateY(70%);
            transform: translateY(70%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@media only screen and (max-width: 768px) {
  .Navbar {
    padding: 1em 20px;
    width: calc(100% - 40px);
    height: calc(100vh - 2em);
    -webkit-transform-origin: left;
            transform-origin: left;
  }

  .Navbar img {
    margin-top: 6px;
  }

  .Navbar ul {
    display: grid;
    grid-template-columns: none;
    grid-template-rows: 80px 80px 80px 80px;
    text-align: center;
  }

  .Navbar ul li span {
    display: none;
  }

  @-webkit-keyframes forNavContainer {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }

  @keyframes forNavContainer {
    from {
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
    }
    to {
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
  }

  @-webkit-keyframes navElements {
    from {
      -webkit-transform: translateX(-70%);
              transform: translateX(-70%);
      opacity: 0;
    }
    to {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes navElements {
    from {
      -webkit-transform: translateX(-70%);
              transform: translateX(-70%);
      opacity: 0;
    }
    to {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
}

.Header {
  overflow: hidden;
  margin: 0;
  padding: 2em 70px;
}

.Header a {
  float: left;
  font-size: 30px;
  color: white;
  font-weight: 600;
  text-decoration: none;
}

.Header img.menu {
  float: right;
  width: 30px;
  margin: 10px 0;
  cursor: pointer;
}

.Header img.logo {
  width: 125px;
}

@media only screen and (max-width: 768px) {
  .Header {
    padding: 1em 20px;
  }

  .Header img.logo {
    width: 80px;
  }
}

.Home {
  margin: 0;
  padding: 0;
  /* position: relative; */
}

.wrapper {
  margin: 0;
  padding: 2em;
  padding-left: 1em;
  top: 10vh;
  position: relative;
  width: 60%;
  margin: auto;
  overflow: hidden;
  border-left: 4px solid rgb(153, 212, 231);
  background-color: rgba(0, 0, 0, 0.548);
}

.Home .wrapper p.quote {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "EB Garamond", serif;
  font-size: 55px;
}

.Home .wrapper p.comma {
  margin: 0;
  padding: 0;
  font-size: 70px;
  font-weight: 600;
  line-height: 0.7;
  color: white;
}

.Home .wrapper p.credit {
  margin: 0;
  padding: 0;
  color: white;
  text-align: right;
}

@media only screen and (max-width: 768px) {
  .Home .wrapper p.quote {
    font-size: 35px;
  }

  .Home .wrapper p.comma {
    font-size: 50px;
  }
}

@media only screen and (max-width: 425px) {
  .Home .wrapper p.quote {
    font-size: 25px;
  }

  .Home .wrapper p.comma {
    font-size: 40px;
  }
}

.Projects {
  position: relative;
  padding: 0;
  margin: 0 auto;
  width: 80vw;
  height: 75vh;
  background: rgba(0, 0, 0, 0.7);
  overflow: scroll;
}

.breadcrumbs {
  width: 80vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  margin: auto;
  overflow: hidden;
  background: rgba(189, 195, 199, 0.8);
}

.breadcrumbs ul {
  margin: 0;
  padding: 0;
  text-align: center;
}

.breadcrumbs ul li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: inline-block;
}

.breadcrumbs ul li a {
  text-decoration: none;
  cursor: default;
}

.breadcrumbs ul li a button {
  margin: 0px 20px;
  padding: 10px;
  font-size: 25px;
  color: white;
  border: none;
  background: rgb(33, 33, 33);
  outline: none;
  cursor: pointer;
}

.breadcrumbs ul li a button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.breadcrumbs ul li a button:active {
  background: rgba(255, 255, 255, 0.5);
}

@media only screen and (max-width: 1024px) {
  .Projects {
    height: 85vh;
  }
}

.ProjChilds {
  margin: 1em;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(3, 1fr);
}

@media only screen and (max-width: 1024px) {
  .ProjChilds {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 425px) {
  .ProjChilds {
    grid-template-columns: repeat(1, 1fr);
  }
}

.Card {
  background: rgba(223, 223, 223, 0.8);
  padding: 0.3em;
  border-radius: 8px;
  overflow: hidden;
}

.Card img {
  width: 100%;
  border-radius: 8px;
}

.Card .details {
  padding: 0.3em;
}

.Card .details .links {
  height: 3em;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
  display: grid;
  grid-template-columns: auto auto auto;
  background-color: #000;
}

.Card .details .links a {
  margin: 0;
  padding: 0;
}

.Card .details .links a button {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(233, 233, 233);
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  border: none;
}

.Card .details .links a:nth-of-type(2) button {
  border: 1px solid rgb(223, 223, 223);
  border-top: none;
  border-bottom: none;
}

.Card .details .links a button:active {
  background-color: rgb(150, 150, 150);
}

.Card .details .links a button:disabled {
  color: rgb(136, 136, 136);
}



